

.search-overlay {
    position: absolute;
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.placeholder {
    position: relative;
    top: 10vh;
    left: 10vw;
    width: 25vw;
    height: 3vh;
    background: #4361EE;
    opacity: 0.5;
    border-radius: 100px;
    padding: 10px;
}

.infinite-scroll {
    overflow: none;
    &-inside{
        width: 100vw;
    }
}


