
    .movies {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: flex-start;
        overflow-y: hidden;
        background: linear-gradient(180deg, rgba(247, 37, 133, 1) -30%, rgba(0, 0, 0, 1) 61.43%);
        &-sidebar {
            position: fixed;
            width: 40px;
            z-index: 1;
        }
        &-main {
            height: 100vh;
            margin-left: 40px;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            overflow-y: hidden;
            &-fav {
                overflow-y: hidden;
                height: 40vw;
            }
            &-row {
                width: 100%;
                overflow-x: hidden;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                &-label {
                    padding-left: 10px;
                    font-family: 'Urbanist';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 30px;
                    height: 5vh;
                    color: #FFFFFF;
                }
                &-movies {
                    max-width: 2000px;
                    width: 100%;
                    height: 25vh;
                    overflow: visible;
                    margin-bottom: 10px;
                }
            }
        }
    }
