

.poster-contener {
    border-radius: 20px;
    width: 140px;
    height: 180px;
    margin-left: 10px;
}
.poster{
    border-radius: 20px;
    width: 140px;
    height: 180px;
    object-fit:cover;
    object-position: 50% 50%;
}

.overlay {
    position: relative;  
    border-radius: 50px;
    background: #fffefe;
    background: rgba(255, 255, 255, 0.7);
    color: #f1f1f1; 
    width: 30px;
    height: 30px;
    transition: .5s ease;
    opacity:0;
    color: white;
    left: 95%;
    bottom: 35%;
}

.poster-contener:hover .overlay {
    opacity: 1;
}
  
.heart {
    position: relative;
    left: 22%;
    top: 22%;
    color: #BDBDBD;
}

.fav {
    position: relative;
    left: 22%;
    top: 22%;
    color: #F72585;
}